
.vp-pager {
    margin: 5px auto;
    font: 12px Tahoma, Helvetica Neue, Hiragino Sans GB, Microsoft Yahei, sans-serif;
    overflow: auto;
.vp-pager-w28 a{
        width: 28px;
}
}
.vp-pager li {
    float: left;
    list-style: none;
}
.vp-pager-item {
    border: 1px solid #dfdfdf;
    border-radius: 2px;
    background-color: #fff;
    margin: 0px 3px;
    height: 28px;
    line-height: 28px;
    font-size: 12px;
}
.vp-pager-item:hover {
    border-color: #5986E1;
}
.vp-pager-point {
    border-color: transparent !important;
    background: transparent !important;
}
.vp-pager-item a {
    display: inline-block;
    text-align: center;
    color: #666;
    height: 28px;
    min-width: 28px;
    line-height: 28px;
    padding: 0 5px;
    text-decoration: none;
}
.vp-pager-point a {
    color: #a3a3a3;
    letter-spacing: 0px;
    width: 12px;
}
.vp-pager-current {
    background: #5986E1;
    border-color: #5986E1;
}
.vp-pager-current a {
    color: #fff;
}
.vp-pager-item-large a {
    padding: 0px 4px;
}
.vp-pager-previous a,
.vp-pager-next a {
    width: 58px;
}
.vp-pager-previous a:before {
    content: '<';
    font-family: consolas;
    margin-right: 2px;
    font-size: 16px;
}
.vp-pager-next a:after {
    content: '>';
    margin-left: 2px;
    font-family: consolas;
    font-size: 16px;
}
.vp-pager-current a,
.vp-pager-point a {
    cursor: default;
}
.vp-pager-shortcut {
    color: #a3a3a3;
}
.vp-pager-shortcut input {
    height: 28px;
    line-height: 28px;
    width: 38px;
    padding: 0px;
    outline: none;
    text-align: center;
    margin: 0px;
    border-radius: 3px;
    border: 1px solid #a3a3a3;
    float: left;
}
.vp-pager-shortcut .vp-pager-shortcut-confirm {
    border-radius: 3px;
    background: #5986E1;
    text-decoration: none;
    text-align: center;
    display: inline-block;
    color: #fff;
    width: 50px;
    height: 28px;
    line-height: 28px;
    margin-left: 5px;
}
.vp-pager-shortcut .vp-pager-shortcut-item {
    float: left;
    height: 28px;
    line-height: 28px;
}



