
.register-title{
    text-align: center;
    font-size: 18px;
}
.register-window{
    width: 346px;
    /*height: 284px;*/
    height: 384px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border-radius: 10px;
}
.register-content{
    padding-top: 20px;
    background-color: #fff;
    height: 290px;
    border-radius: 3px;
    border-top-left-radius: 0;
}
.register-content .register-input,.register-password-helper{
    display: block;
    width: 305px;
    height: 36px;
    margin: 0 auto;
    margin-bottom: 8px;
    background: #F5FAFE;
    border: 1px solid #72BFF0;
    border-radius: 4px;
    padding: 3px;
    padding: 12px 35px 12px 12px;
    color: #69B8EA;
}
.register-password-helper{
    height: auto;
    padding: 0;
    border: 0;
    background: transparent;
    text-align: left;
    font-weight: normal;
}
.register-password-helper input[type="checkbox"]{
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    margin-right: 10px;
}
.register-password-helper label{
    font-weight: normal;
    line-height: 24px;
    height: 24px;
    font-size: 12px;
}
.find-paddword{
    float: right;
}
.input-label{
    display:inline-block;
    width: 80px;
    text-align: right;
}
.register-background{
    background-color: #72BFF0;
    height: 100%;
    background-image: url('./images/dns128x128.png');
    background-repeat: no-repeat;
    background-size: auto 1200px;
    background-position: -400px -400px;
    opacity: 1;
}
.project-name{
    position: absolute;
    top: 20px;
    right: 30px;
    opacity: 0.9;
    font-family: PingFangSC-Medium;
    font-size: 30px;
    color: #FFFFFF;
}
.register-btn{
    width: 306px;
    height: 40px;
    background-color: #72BFF0;
    color:#fff;
    border: 0;
    margin:0 auto;
    display:block;
}
.register-title-item{
    background-color: #fff;
    height: 50px;
    line-height: 50px;
   /* width: 160px;*/
    color: #72BFF0;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.project-name{
    background: url('./images/dns.png') no-repeat;
    padding-left:50px;
    background-size: auto 41px;
}
h1{
    padding:0;margin:0 auto;
    font-size:18px;
    margin-bottom:10px;
    text-align:center;
    width:305px;
}
.lg-error-msg{
    width:305px;
    margin:auto;
    display:block;
}



