
.lg-table thead label {
    color: white;
}
.lg-table thead.multi th {
    border:1px solid white;
}
.lg-table-main,
.lg-table-fixleft,
.lg-table-fixright {
    background-color: white;
}
.lg-table-fixleft,
.lg-table-fixright {
    position: absolute;
    top: 0;
}
.lg-table-main .lg-table {
    table-layout: fixed;
}
.lg-table-fixleft {
    border-right: 1px solid #eee;
}
.lg-table-fixright {
    border-left: 1px solid #eee;
    right: 0;
}
.lg-table span.grid-sort {
    display: inline-block;
    line-height: 13px;
    margin-right: 5px;
    position: relative;
    top: 1px;
    cursor: pointer;
}
.lg-table span.grid-sort:before {
    content: '';
    border-bottom: 6px solid white;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
    border-top: none;
    position: absolute;
    top: 0;
    right: -10px;
}
.lg-table span.grid-sort:after {
    content: '';
    border-top: 6px solid white;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
    border-bottom: none;
    position: absolute;
    bottom: 0;
    right: -10px;
}
.lg-table span.up:before {
    border-bottom: 7px solid white;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    top: 4px;
}
.lg-table span.up:after {
    display: none;
}
.lg-table span.down:before {
    display: none;
}
.lg-table span.down:after {
    border-top: 7px solid white;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    bottom: 2px;
}
.lg-table a {
    margin-right: 5px;
}
.lg-table .lg-ihollowadd,
.lg-table .lg-ihollowminus {
    line-height: 16px;
    font-size: 20px;
    cursor: pointer;
}



