
label[data-v-001cd560]{
    color:#333;
}
.error-msg[data-v-001cd560]{
        color: red;
        font-size: 14px;
        font-style: italic;
        margin-top: 10px;
        margin-left: 20px;
        text-align: left;
}
.login-title[data-v-001cd560]{
        text-align: center;
        font-size: 18px;
}
.login-window[data-v-001cd560]{
        width: 346px;
        /*height: 284px;*/
        height: 384px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        border-radius: 10px;
}
.login-content[data-v-001cd560]{
        padding-top: 40px;
        text-align: center;
        background-color: #fff;
        height: 235px;
        border-radius: 3px;
        border-top-left-radius: 0;
}
.login-input[data-v-001cd560],.login-password-helper[data-v-001cd560]{
        display: block;
        width: 305px;
        height: 36px;
        margin: 0 auto;
        margin-bottom: 8px;
        background: #F5FAFE;
        border: 1px solid #72BFF0;
        border-radius: 4px;
        padding: 3px;
        padding: 12px 35px 12px 12px;
        color: #69B8EA;
}
.login-password-helper[data-v-001cd560]{
        height: auto;
        padding: 0;
        border: 0;
        background: transparent;
        text-align: left;
        font-weight: normal;
}
.login-password-helper input[type="checkbox"][data-v-001cd560]{
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        margin-right: 10px;
}
.login-password-helper label[data-v-001cd560]{
        font-weight: normal;
        line-height: 24px;
        height: 24px;
        font-size: 12px;
}
.find-paddword[data-v-001cd560]{
        float: right;
}
.input-label[data-v-001cd560]{
        display:inline-block;
        width: 80px;
        text-align: right;
}
.login-background[data-v-001cd560]{
        background-color: #72BFF0;
        height: 100%;
        background-image: url('./images/dns128x128.png');
        background-repeat: no-repeat;
        background-size: auto 1200px;
        background-position: -400px -400px;
        opacity: 1;
}
.project-name[data-v-001cd560]{
        position: absolute;
        top: 20px;
        right: 30px;
        opacity: 0.9;
        font-family: PingFangSC-Medium;
        font-size: 30px;
        color: #FFFFFF;
}
.login-btn[data-v-001cd560]{
        width: 306px;
        height: 40px;
        background-color: #72BFF0;
        color:#fff;
        border: 0;
}
.login-title-item[data-v-001cd560]{
        background-color: #fff;
        height: 50px;
        line-height: 50px;
       /* width: 160px;*/
        color: #72BFF0;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
}
.project-name[data-v-001cd560]{
        background: url('./images/dns.png') no-repeat;
        padding-left:50px;
        background-size: auto 41px;
}



