
.vp-tab__nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.vp-tabnav__item {
  cursor: pointer;
  height: 44px;
  line-height: 44px;
  font-size: 14px;
  color: #333;
  text-align: center;
}
.vp-tab--default {
  background-color: #FFFFFF;
}
.vp-tab--default .vp-tabnav__item {
  cursor: pointer;
  height: 44px;
  line-height: 44px;
  font-size: 14px;
  color: #333;
  text-align: center;
  margin: 0 20px;
}
.vp-tab--default .vp-tabnav__item--active {
  cursor: pointer;
  height: 44px;
  line-height: 44px;
  font-size: 14px;
  color: #333;
  text-align: center;
  margin: 0 20px;
  color: #4475E8;
}
.vp-tab--default .vp-tabnav__item--active:after {
  content: "";
  display: block;
  height: 2px;
  background-color: #4475E8;
  margin-top: -1px;
}
.vp-tab--surround {
  border: 1px solid  #DBDEE2;
}
.vp-tab--surround .vp-tabnav__item {
  cursor: pointer;
  height: 44px;
