  line-height: 44px;
  font-size: 14px;
  color: #333;
  text-align: center;
  background-color: #F7F7F8;
  border-right: 1px solid #DBDEE2;
  min-width: 90px;
  background-color: #fff;
  color: #4475E8;
}
